.footer{
  padding: 5.25em 0 2.25em;
  background-color: $black;
  &__top{
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
  }
  &__title{
    font-family: $font2;
    font-weight: 700;
    font-size: 3.5625em;
    line-height: 78%;
    letter-spacing: -0.01em;
    text-transform: uppercase;
    color: #f7f7f7;
    max-width: 5.375em;
    span{
      color: #9494ff;
    }
  }
  &__center{
    margin-top: 6.125em;
  }
  &__address,
  &__conf,
  &__c{
    font-size: .875em;
    line-height: 136%;
    color: rgba(#c4c4c4,.7);
  }
  &__bottom{
    margin-top: 2em;
    padding-top: 2em;
    border-top: 1px solid rgba($white,.08);
    display: flex;
    align-items: center;
    justify-content: flex-end;
  }
  &__logo{
    display: block;
    width: 8.3125em;
    flex-shrink: 0;
    margin-right: auto;
    img{
      width: 100%;
      display: block;
    }
  }
  &__c{
    margin-right: .5em;
  }
  &__conf{
    text-decoration: underline;
  }
}

.footer-nav{
  font-weight: 600;
  ul{
    @include grid100gap(1.625em);
  }
  a{
    font-size: 1em;
    letter-spacing: -0.05em;
  }
}

.footer-socials{
  min-width: 23em;
  &__title{
    font-weight: 700;
    font-size: 1.5625em;
    line-height: 120%;
    letter-spacing: -0.02em;
  }
  &__items{
    display: flex;
    margin-top: 1.875em;
  }
  &__item{
    @include center;
    margin-right: 1.5em;
    &:last-child{
      margin-right: 0;
    }
    svg{
      max-width: 1.875em;
      max-height: 1.875em;
    }
    &:hover{
      path{
        fill: $primary;
      }
    }
  }
}


@media screen and (max-width: 992px){
  .footer{
    padding: 4em 0 2.5em;
    &__top{
      flex-direction: column;
      align-items: flex-start;
    }
    &__title{
      font-size: 2.75em;
      line-height: 111%;
      letter-spacing: -0.02em;
      max-width: 8.375em;
    }
    &__center{
      margin-top: 5.375em;
    }
    &__bottom{
      padding-top: 2.25em;
      margin-top: 4.625em;
    }
    &__logo{
      width: 6.5625em;
    }
  }

  .footer-nav{
    order: 2;
    margin-top: 2.5em;
    a{
      font-size: .9375em;
    }
  }

  .footer-socials{
    margin-top: 3.375em;
    min-width: unset;
    &__title{
      font-size: 1.125em;
    }
    &__items{
      margin-top: 1.25em;
    }
  }
}


@media screen and (max-width: 576px){
  .footer{
    padding-bottom: 1.25em;
    &__center{
      margin-top: 3.5em;
    }
    &__bottom{
      padding-top: 2em;
      margin-top: 4.75em;
      flex-wrap: wrap;
      justify-content: unset;
    }
    &__logo{
      width: 100%;
      margin: 0 0 2em;
      img{
        width: 6.5625em;
      }
    }
  }

  .footer-nav{
    margin-top: 3em;
  }

  .footer-socials{
    margin-top: 2em;
  }
}
