.fg {
  position: relative;
  margin: 0;
  label{
    display: block;
    font-size: .875em;
    line-height: 143%;
    margin-bottom: .375em;
  }
  .nice-select,
  input,
  textarea{
    border-radius: 0.86em;
    border: 1px solid #E1E6EC;
    padding: 0 1.14em;
    height: 3.43em;
    width: 100%;
    font-size: .875em;
    background-color: $white;
    color: #000;
    &::placeholder {
      color: #9AA2B0;
    }
    &:focus {
      border-color: #C4AFFF;
      outline: 1px solid #C4AFFF !important;
    }
  }
  textarea{
    padding-top: 1.19em;
    padding-bottom: 1.19em;
    height: 9em;
    resize: none;
  }
  &__error{
    padding: .25em 1em;
    font-weight: 500;
    color: #DA0916;
    display: none;
    .text12{
      line-height: 100%;
    }
  }
  &._error{
    input{
      background: #fff5f8;
      border-color: #F17C7C;
      outline: none !important;
    }
    .fg__error{
      display: block;
    }
  }
}

.fg-dynamic{
  label{
    position: absolute;
    top: 1em;
    left: 1.14em;
    transition: all .2s;
    pointer-events: none;
    margin-bottom: 0;
    color: #9AA2B0;
  }
}

.fg-dynamic{
  textarea,
  input{
    padding-top: 1.25em;
    &._filled,
    &:focus{
      &~label{
        font-size: .75em;
        top: .58em;
        left: 1.33em;
      }
    }
  }
}

.delivery-item,
.m-radio,
.m-check{
  position: relative;
  input{
    position: absolute;
    opacity: 0;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    z-index: 2;
    cursor: pointer;
  }
}

.m-check,
.m-radio{
  label{
    position: relative;
    cursor: pointer;
    display: flex;
    align-items: center;
    padding-left: 1.4375em;
    min-height: 1em;
    font-weight: 500;
    span{
      font-size: .75em;
      line-height: 150%;
      letter-spacing: -0.03em;;
    }
    &:before,
    &:after{
      pointer-events: none;
    }
    &:before{
      content: '';
      position: absolute;
      z-index: 1;
      left: 0;
      top: 50%;
      transform: translateY(-50%);
      border: 1px solid #e1e6ec;
      width: 1em;
      height: 1em;
      background-color: $white;
      @include bgImgSize(0);
      transition: all .0s;
    }
    a{
      text-decoration: underline;
      position: relative;
      z-index: 2;
    }
  }
}

.m-check {
  input {
    &:checked+label {
      &::before {
        background-color: $black;
        border-color: $black;
        background-size: .5em auto;
      }
    }
  }
  label {
    &:before {
      border-radius: .25em;
      background-image: url(../img/check.svg);
    }
  }
}

.cartcalc{
  padding: .5em .625em;
  border-radius: 4em;
  max-width: 100%;
  width: 7.5625rem;
  display: grid;
  grid-template-columns: 2.5em calc(100% - 2.5em * 2) 2.5em;
  align-items: center;
  height: 3.5em;
  button,
  input{
    height: 100%;
    width: 100%;
    min-height: 100%;
  }
  input{
    border: none;
    background-color: transparent;
    color: $white;
    font-weight: 600;
    font-size: 1.125em;
    letter-spacing: -0.03em;
    text-align: center;
  }
  svg{
    width: 1.5em;
    height: auto;
  }
  button{
    width: 2.5em;
    height: 2.5em;
    background-color: $white;
    border-radius: 50%;
    @include center;
    &:hover{
      background-color: #E6E6EC;
    }
  }
}


