.user,
.delivery{
  margin-top: 2.5em;
  &__title{
    letter-spacing: -0.02em;
    font-weight: 700;
  }
}

.delivery{
  &__items{
    margin-top: .75em;
  }
}

.delivery-item{
  padding: 1em 0;
  display: flex;
  border-bottom: 1px solid #dedfe9;
  &:last-child{
    border: none;
  }
  input:checked~.delivery-item__radio{
    border-color: #9454ff;
    border-width: 5px;
  }
  &__radio{
    background: #fcfcfc;
    width: 1.25em;
    height: 1.25em;
    border-radius: 50%;
    border: 2px solid #c6ced6;
    margin-right: .9375em;
    flex-shrink: 0;
  }
  &__top{
    display: flex;
    align-items: center;
  }
  &__title{
    font-weight: 600;
    font-size: .9375em;
    line-height: 107%;
    letter-spacing: -0.04em;
    min-width: 7em;
    padding-right: .125em;
  }
  &__desc,
  &__duration{
    font-weight: 500;
    font-size: .8125em;
    letter-spacing: -0.05em;
    color: #808590;
  }
  &__desc{
    margin-top: 1em;
  }
  .cart__price{
    margin-left: auto;
    padding-left: 1em;
    font-weight: 500;
    flex-shrink: 0;
    img{
      width: 1.25em;
      margin-right: .5em;
    }
    span{
      font-size: .9375em;
      letter-spacing: -0.06em;
    }
  }
}

.user{
  &__nav{
    margin-top: 1.5em;
  }
  &__btn{
    height: 2.5em;
    padding: 0 1.5em;
    width: auto;
    span{
      font-size: .875em;
    }
  }
}

.user-group{
  margin-top: 1.75em;
  &__title{
    font-weight: 600;
    font-size: .9375em;
    line-height: 160%;
    letter-spacing: -0.01em;
  }
  &__fields{
    margin-top: .75em;
    display: grid;
    grid-template-columns: repeat(4,calc(25% - 1.5em / 4));
    gap: 1em .5em;
    ._col2{
      grid-column: span 2;
    }
  }
}

._full{
  grid-column: 1/-1;
}

._delivery-type-fields{
  display: none;
  &._active{
    display: block;
  }
}

.cart-sidebar{
  padding: 2em 1.5em;
  background: #f3f5f7;
  border-radius: 1.5em;
  border: 1px solid #fcfcfc;
  position: sticky;
  top: -2em;
  &__top{
    padding-bottom: 1.75em;
  }
  &__bottom{
    padding-top: 1.75em;
    border-top: 1px solid #e1e2e4;
  }
  &__btn{
    height: 3.5em;
    margin-top: 1em;
    span{
      font-size: 1em;
    }
  }
  &__check{
    margin-top: 1.5em;
    display: flex;
    justify-content: center;
  }
  &__error{
    font-size: .8125em;
    line-height: 123%;
    letter-spacing: -0.04em;
    text-align: center;
    font-weight: 500;
    color: #d72323;
    margin-top: 1.25em;
  }
}

.order-list{
  @include grid100gap(.9375em);
  &__item{
    display: flex;
    align-items: center;
    justify-content: space-between;
    &-title{
      font-size: .9375em;
      line-height: 107%;
      letter-spacing: -0.04em;
      font-weight: 500;
    }
    .cart__price{
      font-weight: 500;
      img{
        width: 1.25em;
        margin-right: .5em;
      }
      span{
        font-size: .9375em;
      }
    }
  }
}

.order-sum{
  display: flex;
  align-items: center;
  justify-content: space-between;
  &__title{
    font-weight: 600;
    font-size: 1.125em;
    line-height: 89%;
    letter-spacing: -0.04em;
  }
  .cart__price{
    font-weight: 700;
    img{
      width: 1.25em;
      margin-right: .5em;
    }
    span{
      font-size: 1.125em;
    }
  }
}

.wallet{
  min-height: 3em;
  cursor: pointer;
  padding: .5em;
  @include center;
  box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.08), 0 -1px 4px 0 rgba(0, 0, 0, 0.05);
  background-color: $white;
  border-radius: 1em;
  margin-top: 1.5em;
  &__icon{
    width: 2em;
    margin-right: .625em;
  }
  &__num{
    font-weight: 600;
    font-size: .875em;
    line-height: 129%;
    letter-spacing: -0.03em;
  }
  &__arrow{
    width: 1em;
    margin-left: .625em;
  }
}


@media screen and (max-width: 992px){
  .cart-sidebar{
    padding: 0;
    position: static;
    border-radius: 1.5em 1.5em 0 0;
    &__top{
      padding: 1.5em 1.5em 1.25em;
    }
    &__bottom{
      padding: 1.25em 1.5em .75em;
      background-color: inherit;
      position: fixed;
      bottom: 0;
      left: 50%;
      width: calc(100vw - 6.25em);
      transform: translateX(-50%);
      min-height: 15.625em;
    }
    &__btn{
      width: 100%;
    }
  }
}


@media screen and (max-width: 576px){
  .delivery,
  .user{
    margin-top: 2em;
  }

  .user-group{
    &__fields{
      grid-template-columns: repeat(2,calc(50% - .25em));
      gap: .75em .5em;
    }
  }

  .cart-sidebar{
    padding: 0;
    position: static;
    border-radius: 1.5em 1.5em 0 0;
    &__top{
      padding: 1em;
    }
    &__bottom{
      padding: .75em 1em 1em;
      width: calc(100vw - 1em);
    }
    &__btn{
      height: 2.75em;
      margin-top: .5em;
    }
  }

  .order-list{
    gap: .5em;
    &__item{
      .cart__price span,
      &-title{
        font-size: .875em;
      }
    }
  }

  .order-sum{
    .cart__price span,
    &__title{
      font-size: .875em;
    }
  }

  .wallet{
    margin-top: .75em;
  }
}
