.text{
  hr{
    margin: 1.5em 0;
    display: block;
    opacity: 0;
  }
  h3{
    font-size: 1.625em;
    line-height: 123%;
    font-weight: 700;
    margin-bottom: .9em;
  }
  li,
  p{
    font-size: 1em;
    line-height: 156%;
  }
  ul,
  p{
    margin-bottom: 1.25em;
    &:last-child{
      margin-bottom: 0;
    }
  }
  ul{
    padding-left: .5em;
    li{
      padding-left: .8em;
      position: relative;
      &:before{
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        background-image: unset;
        width: unset;
        height: unset;
      }
    }
  }
  a{
    text-decoration: underline;
  }
}


@media screen and (max-width: 992px){
  .text._small,
  .article{
    h3{
      font-size: 1.25em;
      line-height: 120%;
      margin-bottom: 1em;
    }
    h4{
      font-size: 1.0625em;
      line-height: 130%;
    }
    li,
    p{
      font-size: .875em;
      line-height: 143%;
    }
    hr{
      height: .75em;
    }
    blockquote{
      padding: .6875em .6875em .6875em 2.875em;
      &:before{
        top: .6875em;
        left: .6875em;
        width: 1.5625em;
        height: 1.5625em;
      }
      p{
        font-size: .9375em;
        line-height: 173%;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .text{
    p{
      font-size: .9375em;
      line-height: 173%;
    }
  }

  .article{
    blockquote{
      padding: 2.875em .6875em .6875em .6875em;
      p{
        font-size: .875em;
        line-height: 143%;
      }
    }
  }
}
