.invent{
  padding: 5em 0;
  &__inner{
    display: grid;
    grid-template-columns: 35.875em calc(100% - 35.875em - 2.5em);
    gap: 3.5em 2.5em;
  }
  &__items{
    margin-top: 2em;
    @include grid100gap(1.75em);
  }
  &__item{
    display: flex;
    align-items: flex-start;
    &-icon{
      width: 2.5em;
      margin-right: 1.5em;
      flex-shrink: 0;
    }
    .text18{
      line-height: 133%;
      letter-spacing: -.02em;
    }
  }
  &__bottom{
    margin-top: 3.5em;
    @include grid100gap(2.5em);
  }
  &__rate{
    font-size: 2.5em;
    letter-spacing: -.06em;
    font-weight: 600;
  }
  &__nav{
    .m-btn{
      width: 32em;
    }
  }
}

.referral{
  padding: 4.5em 1em 7.75em;
  background-color: #42286F;
  border-radius: 1.375em;
  &__top{
    width: 20em;
    max-width: 100%;
    margin: 0 auto;
    text-align: center;
  }
  &__title{
    font-size: 1.75em;
    letter-spacing: -.06em;
    font-weight: 600;
  }
}

.referral-table{
  margin-top: 3.125em;
  &__grid{
    padding-left: 1.25em;
    padding-right: 1.25em;
    display: grid;
    grid-template-columns: 12em 7.5em calc(100% - 20.5em);
    gap: .5em;
  }
  &__head{
    padding-bottom: .375em;
  }
  &__badge,
  &__badge2,
  &__th{
    width: 8.3125em;
    height: 2.5em;
    @include center;
    border-radius: .875em;
    font-weight: 600;
    color: $black;
  }
  &__th{
    box-shadow: 0 0 27px 0 rgba(235, 155, 255, 0.7);
    background: #eb9bff;
    position: relative;
  }
  &__arrow{
    position: absolute;
    left: 50%;
    top: 100%;
    transform: translateX(-50%);
    width: 1em;
  }
  &__arrow2{
    position: absolute;
    left: 3.375em;
    top: 100%;
    width: 3.1875em;
    margin-top: -.25em;
  }
  &__th2{
    padding-top: .375em;
    padding-left: .8125em;
    font-weight: 600;
    .text14{
      letter-spacing: -0.06em;
    }
  }
  &__row{
    padding-top: 1.5em;
    padding-bottom: 1.5em;
    background-image: url(../img/line.svg);
    background-size: auto .125em;
    background-repeat: repeat-x;
    background-position: left bottom;
    &:nth-child(2){
      .referral-table__col{
        transform: translateX(1.75em);
      }
    }
    &:nth-child(3){
      .referral-table__col{
        transform: translateX(3.5em);
      }
    }
    &:nth-child(4){
      .referral-table__col{
        transform: translateX(5.25em);
      }
    }
    &:last-child{
      background-image: unset;
    }
  }
  &__badge{
    background-color: $white;
    position: relative;
    z-index: 2;
  }
  &__col{
    position: relative;
    display: flex;
  }
  &__badge2{
    display: inline-flex;
    color: $white;
    padding: 0 1em;
    background: #773cdc;
    width: auto;
    .text14{
      letter-spacing: -0.06em;
    }
  }
}


@media screen and (max-width: 992px){
  .invent {
    padding: 4em 0;
    &__inner {
      grid-template-columns: 100%;
    }
    &__bottom{
      margin-top: 2.5em;
    }
    &__nav{
      .m-btn{
        width: 100%;
      }
    }
  }

  .referral{
    padding-left: 3.5em;
    padding-right: 3.5em;
  }
}


@media screen and (max-width: 576px){
  .invent {
    padding: 2.8125em 0;
    &__inner {
      gap: 4em;
    }
    .h2{
      max-width: 97%;
    }
    &__item{
      &-icon{
        width: 2em;
      }
      .text18{
        font-size: .9375em;
      }
    }
    &__rate{
      font-size: 2em;
      text-align: center;
    }
    &__bottom{
      gap: 2em;
    }
  }

  .referral{
    overflow: hidden;
    padding: 2.375em 0 4.75em;
    border-radius: 0;
    width: calc(100% + 2.5em);
    margin-left: -1.25em;
    &__top{
      width: 17.5em;
    }
    &__title{
      font-size: 1.4375em;
    }
  }

  .referral-table{
    margin-top: 2.75em;
    &__grid{
      padding-left: 1em;
      padding-right: 1em;
      grid-template-columns: 8em 4.75em calc(100% - 13.75em);
    }
    &__head{
      padding-bottom: .1875em;
    }
    &__badge,
    &__badge2,
    &__th{
      width: 5.6225em;
      height: 1.75em;
      border-radius: .625em;
      .text14{
        font-size: .5625em;
      }
    }
    &__th{
      box-shadow: 0 0 27px 0 rgba(235, 155, 255, 0.7);
      background: #eb9bff;
      position: relative;
    }
    &__arrow{
      width: .6875em;
    }
    &__arrow2{
      left: 2.25em;
      width: 2.125em;
      margin-top: -.125em;
    }
    &__th2{
      padding-left: .5em;
      padding-top: .25em;
      .text14{
        font-size: .5625em;
      }
    }
    &__row{
      padding-top: 1em;
      padding-bottom: 1em;
      background-image: url(../img/line2.svg);
      background-size: auto .0625em;
      &:nth-child(2){
        .referral-table__col{
          transform: translateX(1.25em);
        }
      }
      &:nth-child(3){
        .referral-table__col{
          transform: translateX(2.5em);
        }
      }
      &:nth-child(4){
        .referral-table__col{
          transform: translateX(3.75em);
        }
      }
    }
    &__badge2{
      display: inline-flex;
      color: $white;
      padding: 0 1em;
      background: #773cdc;
      width: auto;
      .text14{
        letter-spacing: -0.06em;
      }
    }
  }
}
