.conf{
  background: linear-gradient(180.00deg, rgb(131, 70, 222),rgb(52, 4, 124) 20.87%);
  position: relative;
  padding: 9.25em 0 7em;
  &__inner{
    position: relative;
    z-index: 2;
    width: 55em;
    max-width: 100%;
    margin: 0 auto;
  }
  &__title{
    font-weight: 700;
    font-size: 2.5em;
    line-height: 123%;
  }
  .text{
    margin-top: 4.375em;
  }
}


@media screen and (max-width: 992px){
  .conf{
    padding: 8.75em 0 4em;
    .text{
      margin-top: 3.75em;
    }
  }
}


@media screen and (max-width: 576px){
  .conf{
    padding: 7em 0 4em;
    &__title{
      font-size: 2em;
    }
    .text{
      margin-top: 5em;
    }
  }
}
