@import "../vars";
.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0;
    width: 23.375em;
    height: 4.5em;
    max-width: 100%;
    border-radius: 4em;
    font-weight: 600;
    &:focus {
        box-shadow: none;
    }
    span{
        font-size: 1.25em;
        line-height: 90%;
        letter-spacing: -0.03em;
    }
}

.m-btn-white-outline,
.m-btn-white{
    &:hover{
        border-color: #f5f6f6;
        background-color: #f5f6f6;
        color: $black;
    }
    &:active{
        border-color: #eaecee;
        background-color: #eaecee;
        color: $black;
    }
    &:disabled{
        border-color: #dbdbdb;
        background-color: #dbdbdb;
        color: $black;
    }
}

.m-btn-white{
    background-color: $white;
    color: $black;
}

.m-btn-white-outline{
    border: 2px solid $white;
    color: $white;
}

.m-btn-black{
    background-color: $black;
    color: $white;
    &:hover{
        background-color: #282c36;
        color: $white;
    }
    &:active{
        background-color: #060709;
        color: $white;
    }
    &:disabled{
        background-color: #dbdbdb;
        color: $white;
    }
}

.m-btn-violet{
    background-color: #9454ff;
    color: $white;
    &:hover{
        background-color: darken(#9454ff,10%);
        color: $white;
    }
    &:active{
        background-color: darken(#9454ff,15%);
        color: $white;
    }
}


@media screen and (max-width: 576px){
    .m-btn{
        height: 3.5em;
        span{
            font-size: 1em;
        }
    }
}
