.header{
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  padding: .5em 0;
  z-index: 10;
  &__inner{
    display: flex;
    align-items: center;
    justify-content: space-between;
    backdrop-filter: blur(150px);
    background: rgba(42, 43, 62, 0.6);
    padding: .3125em .5em .3125em 1.5em;
    border-radius: .8125em;
  }
  &__logo{
    display: block;
    flex-shrink: 0;
    width: 8.3125em;
    img{
      width: 100%;
      display: block;
    }
  }
}

.header-cart{
  transition: all .3s;
  background: rgba($white, 0.26);
  height: 3.375em;
  display: flex;
  align-items: center;
  padding: 0 1.4375em;
  border-radius: .75em;
  svg{
    width: 1.5em;
    margin-right: .5em;
  }
  &__title{
    font-weight: 600;
    font-size: 1em;
    line-height: 162%;
    letter-spacing: -0.03em;
  }
  &__count{
    transition: all .3s;
    margin-left: .5em;
    height: 1.375em;
    min-width: 1.75em;
    font-weight: 600;
    @include center;
    padding: 0 .25em;
    background: #e6e9f1;
    color: $black;
    border-radius: 2.5em;
    span{
      font-size: .8125em;
      letter-spacing: -0.03em;
    }
  }
  &._active{
    background-color: #dcffff;
    box-shadow: 0 -2px 14px 0 rgba(115, 255, 229, 0.46);
    color: $black;
    path{
      stroke: $black;
    }
    .header-cart__count{
      background: #125970;
      color: $white;
    }
  }
}

.nav{
  font-weight: 600;
  ul{
    display: flex;
  }
  li{
    margin: 0 .625em;
  }
  a{
    font-size: 1em;
    letter-spacing: -0.03em;
  }
}


@media screen and (max-width: 992px){
  .header{
    &__inner{
      padding: .25em .25em .25em 1.5em;
    }
    &__logo{
      width: 6.5625em;
    }
  }

  .nav{
    li{
      margin: 0 .5em;
    }
    a{
      font-size: .8125em;
    }
  }

  .header-cart{
    height: 2.75em;
    padding: 0 .875em;
    &__title{
      font-size: .8125em;
    }
  }
}


@media screen and (max-width: 576px){
  .header{
    padding: .75em 0;
    &__inner{
      padding: .35em .5em .35em .75em;
    }
  }

  .menu{
    display: none;
  }

  .header-cart{
    height: 2.5em;
  }
}
