.modal-bg{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: $vh;
    background: rgba(21, 25, 33, 0.9);
}

.modal {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: calc(100 * var(--vh));
    overflow-y: auto;
    z-index: 80;
    opacity: 0;
    pointer-events: none;
    //transition: all .3s;
    padding: 1em 0;
    text-align: center;

    &:after {
        content: '';
        display: inline-block;
        height: 100%;
        vertical-align: middle;
        margin-right: -4px;
    }
    &__dialog {
        text-align: left;
        vertical-align: middle;
    }

    &._active {
        opacity: 1;
        pointer-events: unset;
        .modal__bg {
            display: block;
        }
    }
    &__dialog {
        background: #12142A;
        width: 31.5em;
        max-width: calc(100% - 1em);
        position: relative;
        z-index: 3;
        padding: 8.75em 7em 6.25em;
        border-radius: 1em;
        display: inline-block;
    }
    &__top{
        display: flex;
        align-items: center;
        justify-content: space-between;
    }
    .close-btn{
        position: absolute;
        top: .5em;
        right: .5em;
    }
    &__icon{
        width: 6em;
        margin: 0 auto;
        display: block;
    }
    &__text{
        margin-top: 2.875em;
        text-align: center;
        @include grid100gap(.875em);
    }
    &__title{
        font-weight: 600;
        font-size: 1.375em;
        line-height: 109%;
        letter-spacing: -0.02em;
    }
    &__desc{
        letter-spacing: -0.02em;
        opacity: .7;
    }
    &__btn{
        max-width: 100%;
        width: 16.125em;
        height: 3.5em;
        margin: 2.875em auto 0;
        span{
            font-size: 1.125em;
        }
    }
    .loader{
        margin: 0 auto;
    }
}

.close-btn{
    width: 1.5em;
    @include center;
    svg{
        width: 100%;
    }
    &:hover{
        path{
            fill: $primary;
        }
    }
}

.loader{
    width: 3em;
    svg{
        width: 100%;
    }
}

.spinner line {
    stroke: $white;
    stroke-width: 4;
    animation: .8s linear infinite fade
}

@keyframes fade {
    from {
        opacity: 1
    }
    to {
        opacity: 0
    }
}

.process-modal{
    .modal__dialog{
        padding-top: 11.25em;
        min-height: 35em;
    }
    .modal__text{
        margin-top: 2.25em;
    }
    .modal__title{
        font-size: 1.125em;
        line-height: 133%;
    }
}



@media screen and (max-width: 992px){
    .modal{
        &__dialog {

        }
        &:after{
            display: none;
        }
    }
}


@media screen and (max-width: 576px){
    .modal{
        &__dialog {
            padding: 4em 2.5em;
        }
    }

    .process-modal{
        .modal__dialog{
            padding-top: 10.5em;
            min-height: 28em;
        }
    }
}
