.order{
  padding: 5em 0;
  &__title{
    text-align: center;
  }
  &__wrap{
    margin-top: 3em;
    padding: 0 3em;
    .container{
      padding-top: 3em;
      padding-bottom: 3em;
      background-color: $white;
      border-radius: 2em;
    }
  }
  &__inner{
    display: grid;
    grid-template-columns: calc(100% - 38.5em) 35em;
    gap: 3.5em;
    align-items: center;
  }
  &__img{
    background-color: #F3F5F7;
    border-radius: 1.875em;
    height: 43.25em;
    padding: 4.5em;
    @include center;
    img{
      max-width: 100%;
      max-height: 100%;
    }
  }
  &__content{
    color: $black;
  }
  &__subtitle{
    text-align: left;
    @media screen and (min-width: 993px){
      font-size: 5em;
      letter-spacing: -.04em;
    }
  }
  &__desc{
    margin-top: 2em;
    font-weight: 500;
    .text20{
      line-height: 140%;
      letter-spacing: -0.05em;
    }
  }
  &__items{
    margin-top: 1.5em;
    @include grid100gap(.75em);
  }
  &__item{
    display: flex;
    align-items: center;
    font-weight: 500;
    img{
      width: 2em;
      margin-right: .75em;
    }
    span{
      font-size: 1em;
      line-height: 112%;
      letter-spacing: -0.04em;
    }
  }
}

.cart{
  //position: relative;
  padding: 1.25em 1em 1em;
  background: #e1e6ec;
  border-radius: 1.75em;
  margin-top: 3.5em;
  &__top{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-left: 1em;
  }
  &__price{
    display: flex;
    align-items: center;
    font-weight: 600;
    img{
      width: 1.75em;
      margin-right: .625em;
    }
    span{
      font-size: 1.5625em;
      letter-spacing: -0.06em;
    }
  }
  &__price2{
    padding: .25em 0 0 2.375em;
    font-weight: 500;
    color: #8c8888;
    span{
      text-decoration: line-through;
      font-size: .9375em;
      line-height: 120%;
      letter-spacing: -0.06em;
    }
  }
  .timer{
    margin-top: 1.5em;
  }
  &__badge{
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    bottom: -1.625em;
    padding: 0 .8125em;
    background: #6767ef;
    border-radius: .625em;
    height: 2em;
    font-weight: 600;
    display: flex;
    align-items: center;
    color: rgba($white,.85);
    span{
      font-size: .9375em;
      line-height: 98%;
      letter-spacing: -0.06em;
    }
  }
}

.cart-nav{
  position: relative;
  width: 16.125em;
  &._added{
    .cartcalc{
      opacity: 1;
      pointer-events: unset;
    }
  }
  &__btn{
    width: 100%;
    height: 3.5em;
    span{
      font-size: 1.125em;
    }
  }
  .cartcalc{
    opacity: 0;
    pointer-events: none;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: #717981;
  }
}

.timer{
  position: relative;
  padding: 1.125em;
  background-color: $white;
  border-radius: 1.5em;
  @include center;
}

.countdown{
  display: flex;
  ._dots{
    font-weight: 500;
    font-size: 1.5em;
    line-height: 94%;
    letter-spacing: -0.06em;
    margin: .125em .67em 0;
  }
}

.countdownProp{
  display: flex;
  &__item{
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  &__num{
    font-weight: 500;
    font-size: 2.0625em;
    line-height: 85%;
    letter-spacing: -0.06em;
  }
  &__title{
    font-weight: 500;
    margin-top: .33em;
    font-size: .875em;
    line-height: 105%;
    letter-spacing: -0.06em;
  }
}

.animtocart{
  width: 4.625em;
  height: 4.625em;
  padding: .25em;
  background: #f3f5f7;
  border-radius: .75em;
  @include center;
  transform: rotate(24deg);
  img{
    max-width: 100%;
    max-height: 100%;
  }
}


@media screen and (max-width: 992px){
  .order{
    padding: 4em 0;
    &__wrap{
      margin-top: 3.5em;
      padding: 0 2.5em;
      .container{
        padding: 3em;
      }
    }
    &__inner{
      grid-template-columns: 100%;
    }
    &__img{
      height: 32.25em;
      width: 27.25em;
      margin: 0 auto;
      max-width: 100%;
      padding: 3.375em;
      border-radius: 1.375em;
    }
  }
}


@media screen and (max-width: 576px){
  .order{
    padding: 3.5em 0;
    &__wrap{
      margin-top: 2.5em;
      padding: 0 1.25em;
      .container{
        padding: .75em;
        border-radius: 1.75em;
      }
    }
    &__inner{
      gap: 1.5em;
    }
    &__img{
      height: 18.5em;
      border-radius: 1.25em;
      padding: 1.75em;
    }
    &__top{
      padding: 0 .75em;
    }
    &__desc{
      margin-top: 1em;
      .text20{
        line-height: 131%;
        letter-spacing: -0.05em;
      }
    }
    &__item{
      span{
        font-size: .9375em;
      }
    }
  }

  .cart{
    margin-top: 2.5em;
    &__top{
      display: block;
    }
    &__badge{
      bottom: -.875em;
      height: 1.75em;
      span{
        font-size: .8125em;
      }
    }
  }

  .cart-nav{
    width: 100%;
    margin: 1em 0 0;
  }

  .countdown{
    ._dots{
      margin: .075em .5em 0;
    }
  }

  .countdownProp{
    &__num{
      font-size: 1.75em;
      line-height: 101%;
    }
    &__title{
      margin-top: .4em;
      font-size: .8125em;
      line-height: 105%;
    }
  }
}
