.cart-modal{
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: calc(100 * var(--vh));
  overflow-y: auto;
  z-index: 80;
  opacity: 0;
  pointer-events: none;
  padding: 4em 0;
  text-align: center;
  &._active {
    opacity: 1;
    pointer-events: unset;
    .cart-modal__bg {
      display: block;
    }
  }
  &._step2{
    .loader{
      opacity: 1;
    }
  }
  &._step3{
    .user{
      opacity: .15;
      pointer-events: none;
    }
  }
  &._step2,
  &._step1{
    .delivery,
    .user{
      opacity: .15;
      pointer-events: none;
    }
  }
  &__close{
    position: fixed;
    top: 1.5em;
    right: 1.75em;
    backdrop-filter: blur(50px);
    background-color: rgba($white,.1);
    width: 3.75em;
    height: 3.75em;
    border-radius: 50%;
    @include center;
    svg{
      width: 2.5em;
    }
  }
  &__dialog{
    padding: 2em;
    width: 75em;
    max-width: calc(100% - 2.5em);
    background-color: $white;
    margin: 0 auto;
    position: relative;
    z-index: 3;
    border-radius: 1em;
    display: inline-block;
    color: $black;
    text-align: left;
  }
  &__grid{
    margin-top: 1em;
    display: grid;
    grid-template-columns: calc(100% - 24.75em) 23.75em;
    gap: .625em 1em;
  }
}

.cart-header{
  padding: .75em 2.5em .75em 1.5em;
  background-color: #F3F5F7;
  border-radius: 1.5em;
  display: flex;
  align-items: center;
  justify-content: space-between;
  &__product{
    display: flex;
    align-items: center;
    &-img{
      width: 4em;
      height: 4em;
      @include center;
      margin-right: .625em;
      flex-shrink: 0;
      img{
        max-width: 100%;
        max-height: 100%;
      }
    }
    &-title{
      font-size: 1.25em;
      font-weight: 600;
      letter-spacing: -.05em;
    }
  }
  .cartcalc{
    grid-template-columns: 2em calc(100% - 4em) 2em;
    width: 10.25em;
    background-color: #717981;
    height: 2.625em;
    padding: .3125em .625em;
    button{
      width: 2em;
      height: 2em;
      svg{
        width: 1.25em;
      }
    }
  }
  .cart__price{
    span{
      font-size: 1.0625em;
    }
  }
}

.cart-content{
  position: relative;
  padding: 2em;
  background-color: #F3F5F7;
  border-radius: 1.5em;
  &__title{
    font-size: 1.5em;
    font-weight: 700;
    letter-spacing: -.02em;
  }
  .address-field{
    margin-top: 1.5em;
  }
  .loader{
    opacity: 0;
    pointer-events: none;
    position: absolute;
    top: 12.5em;
    left: 50%;
    transform: translateX(-50%);
    line{
      stroke: #9C4FFF;
    }
  }
}

.address-field{
  position: relative;
  &._show-result{
    .address-field__dropdown{
      opacity: 1;
      pointer-events: unset;
    }
  }
  &__dropdown{
    position: absolute;
    top: 3em;
    margin-top: .25em;
    box-shadow: 0 24px 44px 0 rgba(0, 0, 0, 0.1);
    background-color: $white;
    width: 100%;
    border-radius: .75em;
    padding: .5em;
    transition: all .3s;
    opacity: 0;
    pointer-events: none;
  }
  &__variant{
    padding: .625em .75em;
    font-weight: 500;
    transition: all .3s;
    border-radius: .5em;
    &:hover{
      cursor: pointer;
      background: #f3f5f7;
    }
    span{
      font-size: .875em;
      line-height: 143%;
    }
  }
}


@media screen and (max-width: 992px){
  .cart-modal{
    padding-top: 5em;
    padding-bottom: 0;
    &__close{
      top: .75em;
      right: 1.25em;
      width: 3.5em;
      height: 3.5em;
      svg{
        width: 2.375em;
      }
    }
    &__dialog{
      padding: 1.75em 1.75em 17em;
      &:before{
        content: '';
        position: fixed;
        bottom: 0;
        left: 1.25em;
        width: calc(100% - 2.5em);
        height: 7em;
        background-color: $white;
        border-radius: 1em;
      }
    }
    &__grid{
      grid-template-columns: 100%;
    }
  }

  .cart-header{
    padding-top: .25em;
    padding-bottom: .25em;
  }
}


@media screen and (max-width: 576px){
  .cart-modal{
    padding: 3.125em 0 0;
    &__close{
      top: .25em;
      right: .875em;
      width: 2.5em;
      height: 2.5em;
      svg{
        width: 1.75em;
      }
    }
    &__dialog{
      max-width: 100%;
      padding: .75em .5em 15em;
      border-bottom-left-radius: 0;
      border-bottom-right-radius: 0;
      &:before{
        width: 100%;
        left: 0;
        border-radius: 0;
      }
    }
    &__grid{
      margin-top: .5em;
    }
  }

  .cart-header{
    padding: .5em 1em 3.25em;
    position: relative;
    &__product{
      &-img{
        width: 3em;
        height: 3em;
        margin-right: .5em;
      }
      &-title{
        font-size: 1em;
      }
    }
    .cartcalc{
      grid-template-columns: 1.75em calc(100% - 3.5em) 1.75em;
      width: 8.25em;
      height: 2.375em;
      border-radius: 1.875em;
      position: absolute;
      left: 50%;
      transform: translateX(-50%);
      bottom: .75em;
      button{
        width: 1.75em;
        height: 1.75em;
        svg{
          width: 1.0625em;
        }
      }
    }
  }

  .cart-content{
    padding: 1.75em 1em;
  }
}
