.error404{
  background: linear-gradient(180.00deg, rgb(131, 70, 222),rgb(52, 4, 124) 100%);
  height: $vh;
  @include center;
  flex-direction: column;
  position: relative;
  overflow: hidden;
  text-align: center;
  &__bg{
    pointer-events: none;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    img{
      width: 100%;
      display: block;
    }
    ._view2{
      display: none;
    }
  }
  &__img{
    width: 15.375em;
  }
  &__num{
    font-weight: 800;
    margin-top: -5em;
    span{
      font-size: 8.75em;
      line-height: 108%;
    }
  }
  &__text{
    width: 36.375em;
    max-width: 100%;
    margin-top: .75em;
    font-weight: 500;
  }
  &__link{
    width: auto;
    padding: 0 3.375em;
    height: 3.5em;
    margin-top: 2.5em;
    span{
      font-size: 1em;
    }
  }
}


@media screen and (max-width: 992px){
  .error404{
    justify-content: unset;
    padding: 8.125em 2.125em 4em;
    &__bg{
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
    }
    &__num{
      margin-top: .5625em;
    }
  }
}


@media screen and (max-width: 576px){
  .error404{
    justify-content: unset;
    padding-top: 6.75em;
    &__bg{
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
    }
    &__img{
      width: 12.8125em;
    }
    &__num{
      margin-top: 1.125em;
      span{
        font-size: 6.875em;
        line-height: 101%;
      }
    }
  }
}
