.scan{
  padding: 0 0 12em;
  position: relative;
  background: #5a1eb4;
  &__bg{
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    pointer-events: none;
    img{
      width: 100%;
      display: block;
    }
    ._view2,
    ._view3{
      display: none;
    }
  }
  .container{
    position: relative;
    z-index: 2;
  }
  &__content{
    width: 50em;
    max-width: 100%;
    margin: -6.5em auto 0;
    text-align: center;
    position: relative;
    z-index: 2;
  }
  .h2{
    font-size: 5.625em;
    line-height: 97%;
  }
  &__desc{
    font-weight: 600;
    margin: .75em auto 0;
    width: 33.875em;
    max-width: 100%;
    .text20{
      line-height: 145%;
      letter-spacing: -0.07em;
    }
  }
  &__nav{
    margin-top: 2.5em;
    display: flex;
    justify-content: center;
  }
  &__img{
    margin: 0 auto;
    width: 31.125em;
    max-width: 100%;
    img{
      width: 100%;
      display: block;
    }
  }
}

.earn{
  padding: 5em 0 8.625em;
  background: linear-gradient(180.00deg, rgb(149, 88, 255),rgb(90, 28, 178) 100%);
  &__top{
    text-align: center;
    width: 39em;
    max-width: 100%;
    margin: 0 auto;
  }
  &__desc{
    font-weight: 500;
    width: 35em;
    max-width: 100%;
    margin: 1.5em auto 0;
    .text19{
      line-height: 142%;
      letter-spacing: -0.07em;
    }
  }
  &__items{
    margin-top: 5em;
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - 2.75em / 3));
    gap: .5em 1.375em;
  }
  &__nav{
    display: flex;
    justify-content: center;
    margin-top: 4em;
  }
}

.earn-card{
  border-radius: 2em;
  background: linear-gradient(180.00deg, rgb(11, 89, 151),rgb(49, 157, 242),rgba(25, 70, 120, 0.62) 100%);
  padding: 1.5em 1.5em 4em;
  overflow: hidden;
  position: relative;
  &:before{
    content: '';
    position: absolute;
    width: 15.5em;
    height: 10.75em;
    background: #a0d7ff;
    top: -9.875em;
    left: 50%;
    transform: translateX(-50%);
    filter: blur(214px);
  }
  &:nth-child(2){
    background: linear-gradient(180.00deg, rgb(101, 0, 229),rgba(75, 0, 156, 0.36) 100%);
    &:before{
      background: #efe3ff;
    }
  }
  &:nth-child(3){
    background: linear-gradient(180.00deg, rgb(137, 89, 18),rgba(96, 64, 5, 0.7) 100%);
    &:before{
      background: #efe3ff;
    }
  }
  &__img{
    position: relative;
    z-index: 2;
    height: 15.25em;
    display: flex;
    justify-content: center;
    padding: .5em;
    img{
      max-width: 100%;
      max-height: 100%;
    }
  }
  &__title{
    font-weight: 700;
    font-size: 3.625em;
    line-height: 90%;
    letter-spacing: -0.02em;
    text-transform: uppercase;
    font-family: $font2;
  }
  &__desc{
    font-size: 1em;
    line-height: 131%;
    letter-spacing: -0.04em;
    font-weight: 500;
    margin-top: 1.5em;
  }
  &__items{
    margin-top: 1.875em;
    @include grid100gap(.75em);
  }
  &__item{
    display: flex;
    &-icon{
      width: 1.5em;
      height: 1.5em;
      @include center;
      margin-right: .75em;
      img{
        width: 100%;
        display: block;
      }
    }
    &-text{
      padding-top: .21875em;
      font-weight: 500;
      .text15{
        letter-spacing: -0.04em;
      }
    }
  }
}


@media screen and (max-width: 992px){
  .scan{
    &__bg{
      bottom: 12em;
      ._view2{
        display: block;
      }
      ._view1{
        display: none;
      }
    }
    .h2{
      font-size: 5.875em;
      line-height: 86%;
    }
  }

  .earn{
    padding-bottom: 5em;
    &__items{
      margin-top: 3.5em;
      grid-template-columns: 100%;
    }
    &__nav{
      margin-top: 3em;
    }
  }

  .earn-card{
    @media screen and (min-width: 577px){
      padding: 3.5em 1.5em;
      display: grid;
      grid-template-columns: 15.625em calc(100% - 17.625em);
      gap: 2em;
      &__img{
        height: 15.25em;
        padding: 0;
        align-items: center;
      }
    }
    &__title{
      font-size: 2.75em;
    }
  }
}


@media screen and (max-width: 576px){
  .scan{
    padding-bottom: 6.25em;
    &__bg{
      bottom: 0;
      ._view3{
        display: block;
      }
      ._view2{
        display: none;
      }
    }
    &__content{
      margin-top: -3em;
    }
    .h2{
      font-size: 3.125em;
      line-height: 100%;
    }
    &__desc{
      .text20{
        line-height: 137%;
        letter-spacing: -0.03em;
      }
    }
    &__img{
      width: 19.5em;
    }
  }

  .earn{
    padding: 3.5em 0;
    &__desc{
      margin-top: .75em;
      .text19{
        line-height: 137%;
        letter-spacing: -0.03em;
      }
    }
    &__items{
      gap: .75em;
      margin-top: 2.5em;
    }
  }

  .earn-card{
    padding-bottom: 2.5em;
    &__img{
      height: 11.625em;
      padding: 0;
      margin-bottom: .625em;
    }
  }
}
