.hero{
  position: relative;
  background: linear-gradient(180.00deg, rgb(46, 143, 255) 45.346%,rgba(152, 71, 255, 0) 88.132%);
  &__bg{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    img{
      width: 100%;
      display: block;
    }
    ._view2,
    ._view3{
      display: none;
    }
  }
}

.main{
  position: relative;
  z-index: 2;
  padding: 6.625em 0 1.125em;
  &__inner{
    display: grid;
    grid-template-columns: 34.125em calc(100% - 34.125em - 4.375em);
    gap: 0 4.375em;
    position: relative;
  }
  &__col{
    padding-top: 6.25em;
  }
  &__coin{
    position: absolute;
    z-index: 3;
    left: 25.375em;
    top: .3125em;
    width: 8.8125em;
  }
  &__coin2{
    position: absolute;
    left: 28em;
    bottom: 18.25em;
    width: 5.3125em;
  }
  .h1{
    position: relative;
    z-index: 2;
  }
  &__desc{
    margin-top: 1.5em;
    max-width: 25em;
    font-weight: 600;
    .text18{
      line-height: 122%;
      letter-spacing: -0.03em;
    }
  }
  &__nav{
    display: flex;
    margin-top: 2.5em;
  }
  &__img{
    width: 40.625em;
    img{
      width: 100%;
      display: block;
    }
  }
}

.how-work{
  position: relative;
  z-index: 2;
  padding-bottom: 6.125em;
  &__top{
    text-align: center;
  }
  &__desc{
    margin: 1.25em auto 0;
    font-weight: 600;
    width: 30em;
    max-width: 100%;
    .text17{
      line-height: 141%;
      letter-spacing: -0.07em;
    }
  }
  &__items{
    margin-top: 4em;
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - 2em / 3));
    gap: .5em 1em;
  }
  &__nav{
    margin-top: 4em;
    display: flex;
    justify-content: center;
  }
}

.step-card{
  padding: .5625em;
  background: #6065e2;
  border-radius: 1.75em;
  &__top{
    border-radius: 1.5em;
    padding-top: 3em;
    background: linear-gradient(180.00deg, rgb(36, 52, 136),rgba(116, 117, 216, 0) 100%);
    height: 22.25em;
    display: flex;
    justify-content: center;
  }
  &__img{
    max-height: 100%;
    max-width: 100%;
    filter: drop-shadow(-2px 14px 44px rgba(0, 0, 0, 0.3));
  }
  &__bottom{
    padding: 1.75em 1.1875em 2em;
  }
  &__title{
    font-size: 1.375em;
    line-height: 109%;
    letter-spacing: -0.04em;
    font-weight: 600;
    max-width: 11.75em;
  }
  &__desc{
    font-size: .9375em;
    line-height: 133%;
    letter-spacing: -0.01em;
    margin-top: 2.67em;
  }
}


@media screen and (max-width: 992px){
  .hero{
    &__bg{
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
    }
  }

  .main{
    padding: 2.625em 0 4em;
    &__inner{
      grid-template-columns: 100%;
    }
    &__col{
      padding: 0;
      margin: -5.5em 0 0 0;
      text-align: center;
    }
    &__col2{
      order: -1;
      padding: 0 3.25em 0 5.375em;
    }
    &__img{
      width: 100%;
    }
    &__desc{
      margin: .75em auto 0;
    }
    &__nav{
      justify-content: center;
    }
    &__coin{
      top: 22.5em;
      left: 0;
    }
    &__coin2{
      left: unset;
      right: -1em;
      bottom: 9.25em;
    }
  }

  .how-work{
    padding: 5em 0 4em;
    &__desc{
      margin-top: .75em;
    }
    &__items{
      margin-top: 3em;
      grid-template-columns: 100%;
    }
    &__nav{
      margin-top: 2.5em;
    }
  }

  .step-card{
    border-radius: 1.5625em;
    padding: .5em;
    display: grid;
    grid-template-columns: 15.75em calc(100% - 18em);
    gap: 0 2.25em;
    &__top{
      padding: 1.125em 0;
      height: 14.875em;
      border-radius: 1.3125em;
    }
    &__bottom{
      padding: 1.25em 1.25em 1.25em 0;
    }
  }
}


@media screen and (max-width: 576px){
  .hero{
    &__bg{
      ._view2{
        display: none;
      }
      ._view3{
        display: block;
      }
    }
  }

  .main{
    padding: 4.875em 0 3.125em;
    &__col{
      margin-top: -1.5em;
    }
    &__col2{
      padding: 0 .3125em 0 1.125em;
    }
    &__desc{
      .text18{
        line-height: 137%;
        letter-spacing: -0.03em;
      }
    }
    &__nav{
      margin-top: 1.5em;
    }
    &__coin{
      top: 14.75em;
      left: -.625em;
      width: 5.8125em;
    }
    &__coin2{
      display: none;
    }
  }

  .how-work{
    padding: 3.125em 0;
    &__desc{
      .text17{
        line-height: 137%;
        letter-spacing: -0.03em;
      }
    }
  }

  .step-card{
    border-radius: 1.75em;
    padding: .4375em;
    grid-template-columns: 100%;
    &__top{
      height: 15.25em;
      border-radius: 1.125em;
      padding: 1.75em 0 .875em;
    }
    &__bottom{
      padding: 1em .8125em 2.5em;
    }
    &__title{
      font-size: 1.25em;
      line-height: 120%;
      letter-spacing: -0.04em;
    }
    &__desc{
      margin-top: 1em;
    }
  }
}
